import { createWebHistory, createRouter } from "vue-router";
import listLdh from "./components/list.vue";
import homeLdh from "./components/homeLdh.vue";
import detailLdh from "./components/detailLdh.vue";
import commentLdh from "./components/commentLdh.vue";
const routes = [
  {
    path: "/list", //접속 경로
    //component: list,
    component: listLdh, //이름
    // beforeEnter: () => {
    //   if (로그인했어요 == false) {
    //     return '/login'
    //   }
    // }
  },
  {
    path: "/home",
    //component: list,
    component: homeLdh,
  },
  {
    //path: "/detail/:id"(\\d+)", url주소에 숫자만 입력받아야 하는 경우
    path: "/detail/:id",
    //component: list,
    component: detailLdh,
    children: [
      //{ path: "jejuHotel", component: JEJUHOTEL },
      { path: "comment", component: commentLdh },
    ],
  },
  {
    //아무 문자나 입력한 경우 홈으로 전송하거나 404페이지 출력
    path: "/:anything(.*)",
    component: homeLdh,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes, //라우터 변수로 만들어서 생성하고 써라
});

export default router;
